.color_Refusee { color: #dc3545; }
.color_Annulee { color: #444; }
.color_Finalisee { color: rgb(253 199 0); }
.color_Facturee { color: rgb(95 21 125); }
.color_Payee { color: rgb(179 88 155); }

.bgColor_EnAttente { background: #28a745; }
.bgColor_EnCours { background: #17a2b8; }
.bgColor_Terminee { background: #007bff; }
.bgColor_Refusee { background: #dc3545; }
