.loginModal .MuiBackdrop-root {
  background: rgba(0, 0, 0, .85);
}

.MuiOutlinedInput-multiline {
  padding: 4px 12px !important;
  background: #ccc;
}

.App-header {
  padding-top: 42px;
}

.App-header header .MuiToolbar-root {
  min-height: 42px;
}

.historyLabel {
  flex: 1;
  border-bottom: 1px solid #777;
  padding: 8px 22px 8px 8px;
  margin-bottom: 4px;
  display: flex;
  flex-wrap: wrap;
  overflow: auto;
}

.historyLabel.payee {
  border-bottom: none;
}

.historyLabel.finalisee,
.historyLabel.facturee,
.historyLabel.payee {
  position: relative;
}

.historyLabel.finalisee:after,
.historyLabel.facturee:after,
.historyLabel.payee:after {
  position: absolute;
  top: 14px;
  right: calc((100vh - 560px) / -3);
  width: calc((100vh - 560px) / 3);
  content: 'label';
  transform: rotate(90deg);
  font-weight: 700;
  transform-origin: top left;
  border-bottom: 1px solid #777;
}

.historyLabel.finalisee:after { content: 'Finalisée'; }
.historyLabel.facturee:after { content: 'Facturée'; }
.historyLabel.payee:after { content: 'Payée'; }

.historyTask {
  border: 1px solid #777;
  font-weight: 700;
  background: #777;
  color: white;
  cursor: pointer;
  padding: 4px 8px;
  margin: 4px;
  min-height: 16px;
  max-height: 28px
}

.NotificationItem {
  border: 1px solid !important;
  background: #eee !important;
  font-weight: 700 !important;
  color: #333;
  padding: 8px 0 8px 16px !important;
  padding-left: 24px !important;
  margin-bottom: 8px !important;
  align-items: center !important;
  text-align: left;
  flex: 1;
  max-height: 64px;
}

.actionBtnWrapper {
  display: flex;
  justify-content: space-around;
  background: #3f50b5;
  color: white;
  height: 56px;
  margin-bottom: 1em;
}

.MuiButton-root.suppressTask {
  color: rgb(244, 67, 54);
  justify-content: center;
  align-items: center;
  position: absolute;
  cursor: pointer;
  display: flex;
  min-width: 0;
  padding: 0;
  top: 8px;
  right: 0px;
  border-radius: 0;
  left: calc(100% - 24px);
  /* transition: left .25s ease-in-out; */
}

.MuiButton-root.suppressTask:hover {
  bottom: 0;
  top: 0;
  left: 0;
  font-size: 18px;
  /* font-weight: 700; */
  zoom: 2;
  padding: 6px 8px;
  color: white;
  transition: left .25s ease-in-out;
  background-color: rgb(244, 67, 54);
}

.MuiButton-root.suppressTask:hover:after {
  content: 'SUPPRIMER';
  margin-left: 8px;
}

.addTaskWrapper {
  justify-content: space-between;
  display: flex;
  align-items: flex-end;
  border: 1px solid #eee;
  margin: 14px -8px;
  padding: 8px;
  border-radius: 8px;
  position: relative;
}

.addTaskWrapper_delete {
  position: absolute;
  right: -8px;
  top: -8px;
  width: 25px;
  height: 25px;
  /* border: 1px solid #ddd;
  background: white;
  border-radius: 4px; */
}

.leftContent,
.centerContent,
.rightContent {
  height: calc(100vh - 42px);
  padding: 1em 2em;
  overflow: hidden;
  position: relative;
}
.rightContent {
  max-width: 375px;
  overflow: auto;
}
.leftContent.full {
  width: 100%;
  max-width: 375px;
  transition: max-width .5s ease;
}
.leftContent.small {
  background: #333;
  max-width: 48px;
  position: relative;
  overflow: visible;
  padding: 4em 0;
  transition: max-width .5s ease;
}

.closeLeftContent {
  position: absolute;
  right: 0;
  background: #333;
  top: 4px;
  z-index: 100;
}

.openLeftContent {
  position: absolute;
  right: 0;
  background: #333;
  top: 4px;
  z-index: 100;
}

.LeftContentSmallCalendar,
.LeftContentSmallHistory,
.LeftContentSmallNotification,
.LeftContentSmallMessage {
  position: absolute;
  padding: 0;
  top: 58px;
  left: 100%;
  background: #333;
  z-index: 150;
  max-width: 350px;
  border: 8px solid #333;
  border-left: none;
}

.LeftContentSmallOpenCalendar,
.LeftContentSmallOpenHistory,
.LeftContentSmallOpenNotification,
.LeftContentSmallOpenMessage {
  transform-origin: 0 0;
  transform: scaleX(1);
  transition: transform .5s ease-in-out;
}

.LeftContentSmallCloseCalendar,
.LeftContentSmallCloseHistory,
.LeftContentSmallCloseNotification,
.LeftContentSmallCloseMessage {
  transform-origin: 0 0;
  transform: scaleX(0);
  transition: transform .5s ease-in-out;
}

.LeftContentSmallHistory,
.LeftContentSmallNotification,
.LeftContentSmallMessage {
  top: 238px;
  padding: 16px;
  border: 1px solid #777;
  border-left: none;
  color: #ddd;
  width: 600px;
  max-width: 600px;
  height: calc(100vh - 280px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-bottom: none;
}

.LeftContentSmallHistory {
  width: 350px;
}

.LeftContentSmallNotification {
  top: 302px;
  height: calc(100vh - 344px);
  width: 350px;
}

.LeftContentSmallMessage {
  top: 366px;
  height: calc(100vh - 408px);
}

.searchInput label,
.searchInput label.Mui-focused,
.searchInput .MuiInput-root,
.searchInput::placeholder,
.searchInput .MuiInput-underline:before,
.searchInput .MuiInput-underline:after {
    border-color: white;
    color: white;
  }

.MuiPickersCalendar-transitionContainer {
  min-height: 220px;
}

.MuiPickersBasePicker-pickerView {
  min-height: 246px;
  align-self: center;
}

.SchedulerDayWrapper {border: 1px solid #ddd;}
.SchedulerDayWrapper .hLines {min-height: 18px; position: relative; font-size: 12px;}
.SchedulerDayWrapper .Hour {border-top: 1px solid #aaa;}
.SchedulerDayWrapper .quartHour {border-top: 1px dotted #c5c5c5;}
.SchedulerDayWrapper .halfHour {border-top: 1px dotted #aaa;}

.SchedulerDayWrapper .hLines.selected {background: #3f50b51f;}

.SchedulerDayWrapper .displayTime {
  position: absolute;
  top: -14px;
  left: -58px;
  padding: 2px 8px;
}

.technicienId {
  min-height: 40px;
  padding: 8px 4px;
  position: sticky;
  z-index: 15;
  background: #3f50b5;
  top: 0px;
  color: white;
  box-shadow: 0px 0px 0px 0px rgb(0 0 0 / 0%), 0px 0px 0px 0px rgb(0 0 0 / 0%), 0px 1px 3px 0px rgb(0 0 0 / 50%);
}

.InterventionCard {
  position: absolute;
  /* background: rgb(63, 81, 181); */
  color: white;
  font-weight: 700;
  padding: 2px 8px;
  margin: 0 4px;
  left: 0;
  right: 0;
  /* border-radius: 4px; */
  z-index: 10;
  cursor: pointer;
  border-radius: 8px;
}

.InterventionCard:hover {
  border: 3px double #eee;
  transform: scale3d(1.10, 1.10, 1.05);
  z-index: 20;
  border-radius: 8px;
  font-size: 11px;
  padding: 0px 8px;
}

.InterventionCard.empty {
  visibility: hidden;
}

.CardHeaderWrapper span {color: #fff;}

.CardHeaderWrapper .MuiCardHeader-title {
    font-weight: 700;
}

.CardHeaderWrapper {
    padding: 2em 1.5em;
}

.returnTechnicien {
  background-color: #eee;
  border: 1px solid #eee;
  padding: .5em 1em;
  margin-top: 4px;
}

.returnTechnicien.errorTechnicien {
  color: rgb(244, 67, 54);
  border-color: rgb(244, 67, 54);
}

.returnTechnicien:empty {
  display: none;
}
